import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)


export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior(to, from, savedPosition) {
    // return 期望滚动到哪个的位置
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  },
  routes: [
    {
      path: "*",
      redirect:{
        path:"/"
      }
    },
    {
      path: "/",
      name: "home",
      component:() => import("./views/Home.vue"),
      meta: { title: 'leewayjean - 首页' },
    },

    {
      path: '/page/:page',
      name: 'article',
      component:() => import("./views/Home.vue"),
    },
    {
      path: "/add",
      name: "add",
      component:() => import("./views/page/addBlog.vue"),
      meta: { title: 'leewayjean - 添加博客' },
    },
    {
      path:"/categories",
      name:"categories",
      component:() => import("./views/page/categories.vue"),
      meta: { title: 'leewayjean - 博客分类' },
    },
    {
      path:"/blogList",
      name:"blogList",
      component:() => import("./views/page/blogList.vue"),
      meta: { title: 'leewayjean - 博客列表' },
    },
    {
      path: "/showblog/:id",
      name: "showblog",
      component:() => import("./views/page/showBlog.vue"),
      meta: { title: 'leewayjean - 展示博客' },
    },
    {
      path: "/editblog/:id",
      name: "editblog",
      component:() => import("./views/page/editBlog.vue"),
      meta: { title: 'leewayjean - 编辑博客' },

    },
    {
      path: "/about",
      component:() => import("./views/page/about.vue"),
      meta: { title: 'leewayjean - 关于' },

    }

  ]
})
